<template>
  <div>
    <div id="top_view">
      <p>发布人基本信息</p>
      <div style="overflow: hidden;">
        <el-row>
          <el-col>
            <div class="publisher_info_item_view">
              <el-col :span="10">
                <label>发布人：</label>
              </el-col>
              <label v-if="isFinishFirst">{{ name }}</label>
              <el-input v-else
                        v-model="name"
                        placeholder="请输入雇主姓名"></el-input>
            </div>
          </el-col>
          <el-col>
            <div class="publisher_info_item_view">
              <el-col :span="10">
                <label>发布手机号：</label>
              </el-col>
              <label v-if="isFinishFirst">{{ phone }}</label>
              <el-input v-else
                        v-model="phone"
                        placeholder="请输入雇主手机号"
                        id="phoneInPut"></el-input>
            </div>
          </el-col>
          <el-col>
            <div class="publisher_info_item_view">
              <el-col :span="10">
                <label>所属公司：</label>
              </el-col>
              <label>{{ companyName }}</label>
            </div>
          </el-col>
          <el-col>
            <div class="publisher_info_item_view">
              <el-col :span="10">
                <label>服务类型：</label>
              </el-col>
              <label v-if="isFinishFirst">{{ serviceName }}</label>
              <el-select v-else
                         v-model="serviceIndex"
                         placeholder="请选择">
                <el-option v-for="(item, index) in serviceList"
                           :key="item.id"
                           :label="item.title"
                           :value="index"></el-option>
              </el-select>
            </div>
          </el-col>
        </el-row>
      </div>
      <div v-show="!isFinishFirst"
           style="text-align: center; margin-top:20px;">
        <el-button @click="nextAction"
                   type="primary">下一步</el-button>
        <el-button @click="cancelAction">取消</el-button>
      </div>
    </div>
    <div id="middle_view"
         v-if="isFinishFirst">
      <p>发布需求信息</p>
      <!-- 第一行开始 -->
      <!-- 母婴护理师 -->
      <el-row v-if="parseInt(this.serviceId) === 1">
        <el-col :span="24">
          <label>预 产 期 ：</label>
          <el-date-picker v-model="dueTime"
                          type="date"
                          placeholder="请选择预产期"></el-date-picker>
        </el-col>
        <el-col :span="24">
          <label>服务期限：</label>
          <el-select v-model="serviceDaysListId"
                     placeholder="请选择服务期限">
            <el-option v-for="item in serviceDaysList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>宝贝数量：</label>
          <el-select v-model="babyraisingBabyNumListId"
                     placeholder="请选择宝贝数量">
            <el-option v-for="item in babyraisingBabyNumList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 家政工 -->
      <el-row v-if="parseInt(this.serviceId) === 2">
        <el-col :span="24">
          <label>服务期限：</label>
          <label>12月</label>
        </el-col>
        <el-col :span="24">
          <label>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 护工 -->
      <el-row v-if="parseInt(this.serviceId) === 4">
        <el-col :span="24">
          <label>服务期限：</label>
          <label>12月</label>
        </el-col>
        <el-col :span="24">
          <label>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>服务对象：</label>
          <el-select v-model="hulistatusListId"
                     placeholder="请选择服务对象">
            <el-option v-for="item in hulistatusList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 育儿嫂 -->
      <el-row v-if="parseInt(this.serviceId) === 3">
        <el-col :span="24">
          <label>服务期限：</label>
          <label>12月</label>
        </el-col>
        <el-col :span="24">
          <label>宝宝数量：</label>
          <el-select v-model="babyNumListId"
                     placeholder="请选择宝宝数量">
            <el-option v-for="item in babyNumList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>宝宝年龄：</label>
          <el-select v-model="babyAgeListId"
                     placeholder="请选择宝宝年龄">
            <el-option v-for="item in babyAgeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>服务方式：</label>
          <el-select v-model="serviceModeListId"
                     placeholder="请选择服务方式">
            <el-option v-for="item in serviceModeList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 第一行结束 -->
      <!-- 第二行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <label>服务薪资：</label>
          <el-select v-model="serviceSalaryListId"
                     placeholder="请选择服务薪资">
            <el-option v-for="item in serviceSalaryList"
                       :key="item.id"
                       :label="item.salary"
                       :value="item.id"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>经验要求：</label>
          <el-select v-model="expListId"
                     placeholder="请选择经验要求">
            <el-option v-for="item in expList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
        <el-col :span="24">
          <label>年龄要求：</label>
          <el-select v-model="ageListId"
                     placeholder="请选择年龄要求">
            <el-option v-for="item in ageList"
                       :key="item.category"
                       :label="item.name"
                       :value="item.category"></el-option>
          </el-select>
        </el-col>
      </el-row>
      <!-- 第二行结束 -->
      <!-- 第三行开始 -->
      <el-row style="margin-top: 10px;">
        <el-col :span="24">
          <div style="display: flex;">
            <label>服务地址：</label>
            <el-input style="flex: 1;"
                      type="textarea"
                      rows="8"
                      resize="none"
                      maxlength="500"
                      placeholder="请输入服务地址信息"
                      v-model="address"></el-input>
          </div>
        </el-col>
      </el-row>
      <!-- 第三行结束 -->
    </div>
    <div id="bottom_view"
         v-if="isFinishFirst">
      <div class="new">
        <div id="name"
             class="form-group">
          <p>籍贯要求：</p>
          <div class="bao">
            <div class="native">
              <div v-for="(val, index) in nativeList"
                   v-bind:key="index"
                   :id="val.code"
                   @click="nativeAction(index, val)"
                   :class="nativeListSelect.indexOf(val.code) === -1 ? '' : 'active'">{{val.name}}</div>
            </div>
          </div>
        </div>
      </div>
      <div class="new">
        <div>
          <span>技能要求：</span>
          <span style="color:#999; font-size: 14px;">请选择1-8个最重要的技能标签。</span>
        </div>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in skillList"
                 v-bind:key="index"
                 :id="val.id"
                 @click="skillAction(index, val)"
                 :class="skillListSelect.indexOf(val.id) === -1 ? '' : 'active'">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div class="new">
        <div>
          <span>性格要求：</span>
          <span style="color:#999; font-size: 14px;">请选择1-6个最重要的性格标签。</span>
        </div>
        <div class="bao">
          <div class="native">
            <div v-for="(val, index) in natureList"
                 v-bind:key="index"
                 :id="val.id"
                 @click="natureAction(index, val)"
                 :class="natureListSelect.indexOf(val.id) === -1 ? '' : 'active'">{{val.name}}</div>
          </div>
        </div>
      </div>
      <div style="display: flex;">
        <label>备注信息：</label>
        <el-input style="flex: 1;"
                  type="textarea"
                  rows="8"
                  resize="none"
                  maxlength="500"
                  placeholder="备注信息"
                  v-model="remark"></el-input>
      </div>
      <div style="text-align: center; margin-top: 10px;">
        <el-button @click="previousAction">上一步</el-button>
        <el-button @click="cancelAction">取消</el-button>
        <el-button type="primary"
                   @click="releaseAction">保存</el-button>
      </div>
    </div>
  </div>
</template>

<script>
import {
  getConditionsService,
  getProvinceRegionList,
  getNatureList,
  getServiceBabelList,
  postReleaseNeeds
} from '../../../service/Management.js'
import { getLoginInfo } from '../../../assets/common/utils'
// 导入请求服务
// 需求条件
function conditionsAction (_this) {
  getConditionsService().then(data => {
    _this.serviceList = data.data.service_type_list
    _this.babyraisingBabyNumList = data.data.babyraising_baby_num_list
    _this.babyNumList = data.data.baby_num_list
    _this.babyAgeList = data.data.baby_age_list
    _this.ageList = data.data.age_list
    _this.expList = data.data.exp_list
    _this.hulistatusList = data.data.hulistatus_list
    _this.serviceModeList = data.data.service_mode_list
    _this.serviceSalaryListAll = data.data.service_salary_list
    _this.serviceDaysList = data.data.service_days_list
  })
  getProvinceRegionList().then(data => {
    _this.nativeList = [{ id: '-1', code: '-1', name: '不限' }]
    _this.nativeList = _this.nativeList.concat(data.data)
  })
  getNatureList().then(data => {
    _this.natureList = data.data
  })
}
// 需求条件之获取技能证书列表
function serviceBabelAction (_this, id) {
  var params = `?service_type_id=${id}&category=skills-label`
  getServiceBabelList(params).then(data => {
    _this.skillList = data.data.items
  })
}
// 发布需求
function releaseNeedsAction (_this) {
  var params = {}
  params['publisher_name'] = _this.name
  params['publisher_phone'] = _this.phone
  params['type_id'] = _this.serviceId
  params['service_date'] = parseInt(_this.serviceId) === 1 ? _this.serviceDaysListId : '12'
  params['remark'] = _this.remark
  params['address_id'] = '0'
  params['address'] = _this.address
  params['native_place_code'] = _this.nativeListSelect.join(',')
  params['skill_ids'] = _this.skillListSelect.join(',')
  params['nature_ids'] = _this.natureListSelect.join(',')
  params['housework_company_id'] = getLoginInfo('company_id')
  if (_this.hulistatusListId !== '') {
    params['nursin_status'] = _this.hulistatusListId
  }
  if (_this.serviceModeListId !== '') {
    params['live_home'] = _this.serviceModeListId
  }
  if (_this.dueTime !== '') {
    params['due_time'] = _this.getDate(_this.dueTime)
  }
  if (_this.babyAgeListId !== '') {
    params['baby_age'] = _this.babyAgeListId
  }
  if (_this.babyraisingBabyNumListId !== '') {
    params['baby_num'] = _this.babyraisingBabyNumListId
  }
  if (_this.babyNumListId !== '') {
    params['baby_num'] = _this.babyNumListId
  }
  if (_this.ageListId !== '') {
    params['service_age'] = _this.ageListId
  }
  if (_this.serviceSalaryListId !== '') {
    params['salary_id'] = _this.serviceSalaryListId
  }
  if (_this.expListId !== '') {
    params['experience_id'] = _this.expListId
  }
  postReleaseNeeds(params).then(data => {
    _this.$router.push('/demandRecords')
  })
}
export default {
  name: 'releaseManagement',
  data () {
    return {
      isFinishFirst: false,
      name: '',
      phone: '',
      address: '',
      remark: '',
      dueTime: '',
      serviceIndex: null,
      serviceId: '',
      serviceName: '',
      serviceList: [],
      dueDate: '',
      babyNumListId: '',
      babyNumList: [],
      babyraisingBabyNumList: [],
      babyraisingBabyNumListId: '',
      babyAgeList: [],
      babyAgeListId: '',
      ageList: [],
      ageListId: '',
      expList: [],
      expListId: '',
      hulistatusList: [],
      hulistatusListId: '',
      serviceModeList: [],
      serviceModeListId: '',
      serviceSalaryListAll: [],
      serviceSalaryList: [],
      serviceSalaryListId: '',
      serviceDaysList: [],
      serviceDaysListId: '',
      nativeListSelect: [],
      nativeList: [],
      natureListSelect: [],
      natureList: [],
      skillListSelect: [],
      skillList: []
    }
  },
  computed: {
    companyName () {
      return getLoginInfo('title')
    }
  },
  watch: {
    serviceIndex (newValue, oldValue) {
      this.serviceName = this.serviceList[newValue].title
      this.serviceId = this.serviceList[newValue].id
    }
  },
  mounted () {
    console.log('11111')
    conditionsAction(this)
  },
  methods: {
    getDate (date) {
      // 日期格式转换
      let d = new Date(date)
      let M = (d.getMonth() + 1) < 10 ? '0' + (d.getMonth() + 1) : (d.getMonth() + 1)
      let day = d.getDate() < 10 ? '0' + d.getDate() : d.getDate()
      let h = d.getHours() < 10 ? '0' + d.getHours() : d.getHours()
      let m = d.getMinutes() < 10 ? '0' + d.getMinutes() : d.getMinutes()
      let s = d.getSeconds() < 10 ? '0' + d.getSeconds() : d.getSeconds()
      let returnDate = d.getFullYear() + '-' + M + '-' + day + ' ' + h + ':' + m + ':' + s
      return returnDate
    },
    nextAction () {
      const PHONENUMBER = /^1(3|4|5|6|7|8|9)\d{9}$/
      if (this.name === null || this.name === '') {
        this.$message.error('请输入雇主姓名')
        return
      }
      if (this.phone === null || this.phone === '') {
        this.$message.error('请输入雇主电话')
        return
      } else if (!PHONENUMBER.test(this.phone)) {
        this.$message.error('请输入格式正确的雇主手机号码')
        return
      }
      if (this.serviceId === null || this.serviceId === '') {
        this.$message.error('请选择服务类型')
        return
      }
      this.isFinishFirst = true
      this.serviceSalaryList = this.serviceSalaryListAll.filter(
        item => item.service_type_id === parseInt(this.serviceId)
      )
      this.serviceSalaryList.sort(function (a, b) {
        return (a.salary - b.salary)
      })
      if (this.serviceSalaryList.length > 0) {
        this.serviceSalaryListId = this.serviceSalaryList[0].id
      }
      if (this.expList.length > 0) {
        this.expListId = this.expList[0].category
      }
      serviceBabelAction(this, this.serviceId)
    },
    previousAction () {
      this.isFinishFirst = false
      this.clearnDemandInfo()
    },
    clearnDemandInfo () {
      this.dueTime = ''
      this.serviceDaysListId = ''
      this.babyraisingBabyNumListId = ''
      this.serviceModeListId = ''
      this.hulistatusListId = ''
      this.babyNumListId = ''
      this.serviceSalaryListId = ''
      this.expListId = ''
      this.ageListId = ''
      this.address = ''
      this.skillListSelect = []
      this.nativeListSelect = []
      this.natureListSelect = []
      this.remark = ''
    },
    // serviceChange (id) {
    //   serviceBabelAction(this, id)
    // },
    cancelAction () {
      this.$router.push('/demandRecords')
    },
    nativeAction (index, val) {
      if (val.code === '-1') {
        let id = val.code
        if (this.nativeListSelect.indexOf('-1') !== -1) {
          this.nativeListSelect = []
        } else {
          this.nativeListSelect = []
          this.nativeListSelect.push(id)
        }
      } else {
        let id = val.code
        let indexTag = this.nativeListSelect.indexOf(id)
        if (this.nativeListSelect.indexOf(id) === -1) {
          if (this.nativeListSelect.indexOf('-1') !== -1) {
            this.nativeListSelect = []
            this.nativeListSelect.push(id)
          } else {
            this.nativeListSelect.push(id)
            if (this.nativeListSelect.length === this.nativeList.length - 1) {
              this.nativeListSelect = ['-1']
            }
          }
        } else {
          this.nativeListSelect.splice(indexTag, 1)
        }
      }
    },
    skillAction (index, val) {
      let code = val.id
      let indexTag = this.skillListSelect.indexOf(code)
      if (this.skillListSelect.indexOf(code) === -1) {
        if (this.skillListSelect.length === 8) {
          this.$message.error('技能标签最多为8个')
          return
        }
        this.skillListSelect.push(code)
      } else {
        this.skillListSelect.splice(indexTag, 1)
      }
    },
    natureAction (index, val) {
      let code = val.id
      let indexTag = this.natureListSelect.indexOf(code)
      if (this.natureListSelect.indexOf(code) === -1) {
        if (this.natureListSelect.length === 6) {
          this.$message.error('性格标签最多为6个')
          return
        }
        this.natureListSelect.push(code)
      } else {
        this.natureListSelect.splice(indexTag, 1)
      }
    },
    releaseAction () {
      if (parseInt(this.serviceId) === 1) {
        if (this.dueTime === null || this.dueTime === '') {
          this.$message.error('请选择预产期')
          return
        }
        if (this.serviceDaysListId === '' || this.serviceDaysListId === null) {
          this.$message.error('请选择服务期限')
          return
        }
        if (this.babyraisingBabyNumListId === '' || this.babyraisingBabyNumListId === null) {
          this.$message.error('请选择宝贝数量')
          return
        }
      } else if (parseInt(this.serviceId) === 2) {
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
      } else if (parseInt(this.serviceId) === 4) {
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
        if (this.hulistatusListId === '' || this.hulistatusListId === null) {
          this.$message.error('请选择服务对象')
          return
        }
      } else if (parseInt(this.serviceId) === 3) {
        if (this.babyNumListId === '' || this.babyNumListId === null) {
          this.$message.error('请选择宝宝数量')
          return
        }
        if (this.serviceModeListId === '' || this.serviceModeListId === null) {
          this.$message.error('请选择服务方式')
          return
        }
      }
      if (this.serviceSalaryListId === '' || this.serviceSalaryListId === null) {
        this.$message.error('请选择服务薪资')
        return
      }
      if (this.expListId === '' || this.expListId === null) {
        this.$message.error('请选择经验要求')
        return
      }
      if (this.ageListId === '' || this.ageListId === null) {
        this.$message.error('请选择年龄要求')
        return
      }
      if (this.address === '' || this.address === null) {
        this.$message.error('请输入服务地址')
        return
      }
      if (this.nativeListSelect.length === 0) {
        this.$message.error('请选择籍贯要求')
        return
      }
      if (this.skillListSelect.length === 0) {
        this.$message.error('请选择技能要求')
        return
      }
      if (this.natureListSelect.length === 0) {
        this.$message.error('请选择性格要求')
        return
      }
      releaseNeedsAction(this)
    }
  }
}
</script>

<style scoped>
#top_view,
#middle_view,
#bottom_view {
  text-align: left;
  padding: 0px 15px;
}
.el-col label,
.el-col span {
  line-height: 40px;
  width: 100px;
}
.publisher_info_item_view {
  float: left;
  display: flex;
  padding-right: 50px;
  box-sizing: border-box;
  width: 100%;
  flex-direction: row;
  /* margin-top: 5px; */
  /* height: 40px; */
}
.publisher_info_item_view label {
  line-height: 40px;
}
.publisher_info_item_view .el-input,
.publisher_info_item_view .el-select {
  flex: 1;
}
.new {
  padding-bottom: 40px;
  text-align: left;
}
.new .bao {
  display: inline-block;
  width: 90%;
}
.new .bao .native,
.nature,
.skillList {
  display: flex;
  flex-wrap: wrap;
}
.new .bao .native div,
.new .bao .nature div,
.new .bao .skillList div {
  /* width: px; */
  padding: 3px 10px;
  height: 28px;
  cursor: pointer;
  margin-right: 12px;
  margin-top: 16px;
  border: 1px solid #f0f0ee;
  border-radius: 17px;
  line-height: 28px;
  text-align: center;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
.active {
  border-color: #1cb394 !important;
  color: #1cb394;
}
#top_view >>> #phoneInPut::placeholder {
}
</style>



